import React from 'react';
import { default as MuiBackdrop, BackdropProps } from '@mui/material/Backdrop';

const Backdrop = React.forwardRef<HTMLElement, BackdropProps>( ( props, ref ) => {
	return (
		<MuiBackdrop
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<BackdropProps>;

export default Backdrop;
